import axios from "axios";

function readAll() {
    return new Promise(resolve => {
        axios.get("http://localhost:4000/rows/all")
        .then(response => response.status === 200 && response.data.Items)
        .then(resolve)
    })
}

function writeItem(body) {
    return new Promise(resolve => {
        axios.post("http://localhost:4000/rows/write", body)
        .then(response => response.status === 200 && response.data)
        .then(resolve)
    })
}

export {
    readAll, 
    writeItem
}