import { useEffect } from "react";
import { useAuth0 } from "@auth0/auth0-react";
import { useAppContext } from "./context"
import './App.css';

const LoginButton = () => {
  const { loginWithRedirect, isAuthenticated } = useAuth0();

  return !isAuthenticated &&  <button  className="btn btn-info" onClick={() => loginWithRedirect()}>Log In</button>;
};

const LogoutButton = () => {
  const { logout, isAuthenticated } = useAuth0();

  return (
    isAuthenticated && <button className="btn btn-info mb-5" onClick={() => logout({ returnTo: window.location.origin })}>
      Log Out
    </button>
  );
};

const Profile = () => {
  const { items } = useAppContext()
  const { user, isAuthenticated, isLoading } = useAuth0();

  if (isLoading) {
    return <div>Loading ...</div>;
  }

  return (
    isAuthenticated && (
      <div>
        <img className="img_profile" src={user.picture} alt={user.name} />
        <h2>{user.name}</h2>
        <p>{user.email}</p>
        <hr />
        <ul>
          {items.map(item => {
            return(<li>{item.category}</li>)
          })}
        </ul>
      </div>
    )
  );
};


function App() {
  const { fetchItems } = useAppContext()
  useEffect(() => fetchItems(), [fetchItems])
  return (
    <div className="App d-flex flex-column justify-content-center align-items-center">
      <LoginButton />
      <LogoutButton />
      <Profile />
    </div>
  );
}

export default App;
